@charset "UTF-8";
.app-container {
  display: flex;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -moz-flex;
  display: -webkit-flex;
  -ms-box-orient: horizontal;
  justify-content: space-between;
  -webkit-justify-content: space-between;
  flex-direction: column;
  height: 100%;
  min-height: 100vh;
  background-color: #222222;
  background-size: cover; }

.app-today {
  flex-direction: row;
  z-index: 100; }

.app-list-graph {
  flex-direction: row;
  margin: 0 25px;
  z-index: 100; }

.app-list-graph > div {
  flex-basis: 50%; }

.app-instructions {
  font-size: 1.1rem;
  margin: auto;
  text-align: center;
  color: white; }

.carousel-next:before, .carousel-prev:before {
  background-image: none;
  color: #FFFFFF;
  font-size: 18px;
  font-size: 32px;
  font-weight: 600; }

.carousel-prev:before {
  content: "↩"; }

.carousel-next:before {
  content: "↪"; }

