.clouds {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: block; }

.clouds1 {
  background: transparent url(/img/clouds1.png) repeat top center;
  z-index: 0;
  animation: MoveClouds 200s linear infinite; }

.clouds1.on-github {
  background: transparent url(/climately-app/img/clouds1.png) repeat top center; }

.clouds2 {
  background: transparent url(/img/clouds2.png) repeat top center;
  z-index: 0;
  animation: MoveClouds 400s linear infinite; }

.clouds2.on-github {
  background: transparent url(/climately-app/img/clouds2.png) repeat top center; }

.rain {
  z-index: 100; }

.drop {
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 50%);
  background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(rgba(255, 255, 255, 0)), to(rgba(255, 255, 255, 0.3)));
  background: -moz-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
  width: 1px;
  height: 90px;
  position: fixed;
  bottom: 200px;
  animation: RainFall 0.65s linear infinite;
  -webkit-animation: RainFall 0.65s linear infinite;
  -moz-animation: RainFall 0.65s linear infinite;
  z-index: 100; }

